<template>
	<div class="content2">
		<el-table :data="$store.state.exchanges.records" border style="width: 100%; margin-top: 20px">
			<el-table-column  label="调出部门信息">
				<el-table-column label="部门">
					<template slot-scope="scope">
							{{scope.row.from.name}}
					</template>
				</el-table-column>
				<el-table-column label="项目" width="120px;">
					<template slot-scope="scope">
						{{typeMap[scope.row.to.catalog]}}
					</template>
				</el-table-column>
				<el-table-column label="调整前" width="100px;">
					<template slot-scope="scope">
						{{scope.row.from.stock}}
					</template>
				</el-table-column>
				<el-table-column label="调整后" width="100px;">
					<template slot-scope="scope">
						{{scope.row.from.balance}}
					</template>
				</el-table-column>
			</el-table-column>

			<el-table-column width="100px;">
				<el-table-column label="金额">
					<template slot-scope="scope">
						{{scope.row.from.amount}}
					</template>
				</el-table-column>
			</el-table-column>
			
			<el-table-column label="调人部门信息">
				<el-table-column label="部门">
					<template slot-scope="scope">
						{{scope.row.to.name}}
					</template>
				</el-table-column>
				<el-table-column label="项目" width="120px;">
					<template slot-scope="scope">
						{{typeMap[scope.row.to.catalog]}}
					</template>
				</el-table-column>
				<el-table-column label="调整前" width="100px;">
					<template slot-scope="scope">
						{{scope.row.to.stock}}
					</template>
				</el-table-column>
				<el-table-column label="调整后" width="100px;">
					<template slot-scope="scope">
						{{scope.row.to.balance}}
					</template>
				</el-table-column>
			</el-table-column>
			<el-table-column>
				<el-table-column label="调整时间">
					<template slot-scope="scope">
						{{parseTime(scope.row.createTime)}}
					</template>
				</el-table-column>
			</el-table-column>
		</el-table>
		<el-pagination :current-page="page" :page-size="15" layout="total, prev, pager, next, jumper" :total="$store.state.exchanges.count"
			align="right" @current-change="changePage" class="pagi">
		</el-pagination>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				page: 1,
				count: 0,
				limit: 10,
				yearValue: new Date(),
				records: [],
				typeMap: {
					trip: '差旅费',
					benefits: '福利费',
					others: '其他费用'
				},
			};
		},
		methods: {
			parseTime(date) {
				date = new Date(date)
				return `${date.getFullYear()}-${date.getMonth() +1}-${date.getDate()} ${date.getHours()}:${date.getMinutes()}`
			},
			getTypeLists() {
        this.$http.get(`/ec/api/types?type=budgets`).then(res => {
          let resData = res.data;
          if (resData.errcode === 0) {
            for(let item of (resData.data || [])) {
							this.typeMap[item.type]  = item.name;
						}
          }
        })
      },
			getExchangeLists() {
				this.$http.get(`/ec/api/exchange?page=${this.page}&limit=${this.limit}&year=${this.year}&keywords=${this.keywords}`).then(res =>{
					let resData = res.data;
          if (resData.errcode === 0) {
						this.$store.commit('setExchanges', {
							count: resData.data.count || 0,
							records: resData.data.rows ||[]
						});
            // this.count = resData.data.count || 0;
						// this.records = resData.data.rows ||[];
					}
				})
			},
			changePage(page) {
        this.page = page;
        this.getExchangeLists()
      }
		},
		created() {
			this.getTypeLists()
			this.getExchangeLists();
		}
	};
</script>
<style scoped>
	.pagi {
		margin: 10px;
	}
</style>